@charset "UTF-8";
/* breakpoints */
*,
*::before,
*::after {
  box-sizing: border-box;
}

*:active,
*:focus {
  outline: none;
}

html {
  min-height: 100vh;
  height: 100%;
  overflow-x: hidden;
}

body {
  font-family: "Inter";
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.2;
  color: #121212;
  height: 100%;
  background-color: #FFFFFF;
  overflow-x: hidden;
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: min-content min-content 1fr;
}
body.modal_open {
  height: 100vh;
  overflow: hidden;
}

h1, h2, h3, h4 {
  margin: 0;
}

a, input {
  font-family: "Inter", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.2;
  text-decoration: none;
  color: inherit;
  transition: all 0.3s ease;
}

a:hover {
  color: #43B0EE;
}

input:focus {
  outline: none;
}

p {
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

button {
  padding: 0;
  background-color: transparent;
  border: none;
}

@media (max-width: 1023.5px) {
  body, a, input {
    font-size: 14px;
  }
}
@media (max-width: 599.5px) {
  body, a, input {
    font-size: 12px;
  }
}
.wrapper {
  width: 1244px;
  margin-inline: auto;
}

.h2 {
  font-size: 36px;
  font-weight: 600;
  margin: 0;
}

.section {
  margin-bottom: 88px;
}

.page-title {
  margin-block: 48px 32px;
}

.section-title {
  margin-bottom: 56px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
}
.section-title__link {
  font-size: 16px;
  color: #31A0DF;
}

.btn {
  font-size: 16px;
  line-height: 1.2;
  width: 100%;
  padding: 15px 24px;
  background-color: #FCD403;
  border: none;
  border-radius: 30px;
  transition: background-color 0.3s ease;
}
.btn:hover {
  background-color: #F3DA56;
  outline: none;
}
.btn:focus, .btn:active {
  outline: none;
  box-shadow: none;
}

.phone-block {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.phone-block > a {
  font-size: 20px;
  font-weight: 500;
}
.phone-block > p {
  font-size: 12px;
  font-weight: 300;
}

.sticky {
  position: fixed;
  z-index: 5;
}

.stop {
  position: absolute;
  z-index: 5;
}

.add2cart {
  display: grid;
}
.add2cart > * {
  grid-column: 1/2;
  grid-row: 1/2;
}
.add2cart.added .card__qty {
  z-index: 1;
}

@media (max-width: 1439.5px) {
  .wrapper {
    width: 976px;
  }
  .h2 {
    font-size: 32px;
  }
  .section {
    margin-bottom: 72px;
  }
  .page-title {
    margin-block: 32px;
  }
  .section-title {
    margin-bottom: 32px;
  }
}
@media (max-width: 1023.5px) {
  .wrapper {
    width: 568px;
  }
  .h2 {
    font-size: 32px;
  }
  .section {
    margin-bottom: 64px;
  }
  .page-title {
    margin-block: 24px;
  }
  .section-title {
    margin-bottom: 24px;
  }
}
@media (min-width: 599.5px) {
  .from-sm {
    display: none;
  }
}
@media (max-width: 599.5px) {
  .wrapper {
    width: 336px;
  }
  .h2 {
    font-size: 28px;
  }
  .section {
    margin-bottom: 48px;
  }
