.spage {
  font-size: 16px;
}
.spage br {
  font-size: 0.5em;
  line-height: 1;
  display: block;
}
.spage p:not(.note, .pin, .attention, .danger, .quote) {
  margin-top: 16px;
}
.spage p:not(.note, .pin, .attention, .danger, .quote) + p:not(.note, .pin, .attention, .danger, .quote) {
  margin-top: 8px;
}
.spage h2 {
  font-size: 24px;
  font-weight: 600;
  line-height: 1.1;
  margin-top: 24px;
}
.spage strong,
.spage b {
  font-size: 18px;
  font-weight: 500;
}
.spage small {
  font-size: 14px;
}
.spage img {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
  margin-top: 16px;
}
.spage ul {
  margin-top: 16px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.spage ul.inline {
  flex-direction: row;
  gap: 24px;
  align-items: center;
}
.spage ul.inline li {
  padding: 0;
}
.spage ul.inline li::before {
  content: none;
}
.spage ul ul {
  margin-top: 8px;
  gap: 8px;
}
.spage li {
  padding-left: 12px;
  position: relative;
}
.spage li::before {
  content: "";
  width: 4px;
  height: 4px;
  background-color: #31A0DF;
  border-radius: 50%;
  display: block;
  position: absolute;
  left: 0;
  top: 7px;
}
.spage .note {
  font-size: 16px;
  padding: 12px 16px;
  margin-top: 16px;
  background: #D0E6F3;
  border-radius: 5px;
}
.spage .quote {
  font-size: 16px;
  padding: 12px 16px;
  margin-top: 16px;
  background: #D0E6F3;
  border-radius: 5px;
  display: flex;
  gap: 8px;
}
.spage .quote::before, .spage .quote::after {
  font-size: 26px;
  font-weight: 600;
  line-height: 1.1;
  color: #31A0DF;
}
.spage .quote::before {
  content: "«";
}
.spage .quote::after {
  content: "»";
  align-self: flex-end;
}
.spage .pin,
.spage .attention,
.spage .danger {
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  padding: 16px;
  margin-top: 16px;
  display: flex;
  align-items: flex-start;
  gap: 16px;
  border-radius: 5px;
}
.spage .pin::before,
.spage .attention::before,
.spage .danger::before {
  content: "";
  width: 24px;
  height: 24px;
  display: block;
  background: transparent no-repeat center;
  background-size: 100%;
  flex-shrink: 0;
}
.spage .pin {
  background-color: #D0E6F3;
}
.spage .pin::before {
  background-image: url("../img/pin.svg");
}
.spage .attention {
  background-color: #FFF6C7;
}
.spage .attention::before {
  background-image: url("../img/attention.svg");
}
.spage .danger {
  background-color: #F9C7C7;
}
.spage .danger::before {
  background-image: url("../img/danger.svg");
}
.spage__btn {
  width: max-content;
  margin-top: 12px;
}

@media (min-width: 1439.5px) {
  .spage {
    width: 823px;
  }
}
@media (max-width: 1023.5px) {
  .spage h2 {
    font-size: 18px;
  }
}
@media (max-width: 599.5px) {
  .spage {
    font-size: 14px;
  }
  .spage h2 {
    font-size: 16px;
  }
  .spage strong,
  .spage b {
    font-size: 16px;
  }
  .spage small {
    font-size: 12px;
  }
  .spage .pin,
  .spage .attention,
  .spage .danger {
    font-size: 16px;
    padding: 12px;
    flex-direction: column;
    gap: 12px;
  }
  .spage .pin::before,
  .spage .attention::before,
  .spage .danger::before {
    width: 16px;
    height: 16px;
  }
  .spage__btn {
    margin-top: 8px;
  }
}
