.modal {
  overflow: hidden;
  display: block;
  transform: translateX(100%);
  transition-delay: 0.3s;
}
.modal.open {
  transform: translateX(0);
  transition: transform 0s;
}
.modal.open .modal__overlay {
  opacity: 1;
}
.modal.open .modal__body {
  transform: translateX(0);
}
.modal__overlay {
  position: absolute;
  inset: 0;
  background: rgba(18, 18, 36, 0.4);
  opacity: 0;
  transition: opacity 0.3s ease;
}
.modal__body {
  width: 607px;
  padding: 260px 98px 32px 98px;
  background-color: #FFFFFF;
  border-radius: 10px 0px 0px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transform: translateX(100%);
  transition: transform 0.3s ease;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  overflow: auto;
}
.modal__close {
  position: absolute;
  top: 16px;
  right: 16px;
}
.modal__caption {
  font-size: 20px;
  font-weight: 600;
  line-height: 110%;
  text-align: center;
}
.modal__text {
  font-size: 14px;
  text-align: center;
  margin-top: 16px;
}
.modal__form {
  width: 100%;
  margin-top: 32px;
  display: flex;
  flex-direction: column;
}
.modal__form-group {
  width: 100%;
  height: 51px;
  padding: 7px 16px;
  margin-top: 12px;
  border-radius: 10px;
  border: 1px solid #D3D3D3;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  position: relative;
}
.modal__form-group:first-child {
  margin-top: 0;
}
.modal__form-group.focus {
  border-color: #31A0DF;
}
.modal__form-group--textarea {
  height: auto;
  min-height: 51px;
}
.modal__form-group.error {
  border-color: #F02E2E;
}
.modal__label {
  font-size: 14px;
  color: #ADADAD;
  width: 100%;
  display: flex;
  justify-content: space-between;
  transition: all 0.3s ease;
  cursor: pointer;
}
.modal__label > span {
  color: #F02E2E;
}
.modal__input, .modal__textarea {
  font-size: 16px;
  color: #121212;
  width: 100%;
  border: none;
  padding: 0;
  height: 0;
  min-height: 0;
}
.modal__input {
  transition: all 0.3s ease;
}
.modal__textarea {
  transition: min-height 0.3s ease;
}
.modal__form-group.open .modal__label {
  font-size: 12px;
}
.modal__form-group.open .modal__label > svg {
  display: none;
}
.modal__form-group.open .modal__input {
  height: 19px;
  margin-top: 4px;
}
.modal__form-group.open .modal__textarea {
  min-height: 19px;
  margin-top: 4px;
}
.modal label.error {
  font-size: 14px;
  color: #F02E2E;
  margin-top: 8px;
  margin-left: 16px;
}
.modal__submit {
  margin-top: 16px;
}
.modal__note {
  font-size: 14px;
  margin-top: 12px;
}
.modal__note > a {
  font-size: inherit;
  color: #31A0DF;
}
.modal__note > a:hover {
  color: #43B0EE;
}
.modal__btn {
  margin-top: 32px;
}
.modal__link {
  font-size: 14px;
  color: #31A0DF;
  text-align: center;
  margin-top: 12px;
}
.modal__link:hover {
  color: #43B0EE;
}
.modal__link:disabled {
  color: #A4D4F0;
}
.modal__time {
  font-size: 12px;
  text-align: center;
  color: #ADADAD;
  margin-top: 4px;
}

